import React, { useState } from 'react';

const Contact = () => {

  const [data, setData] = useState({
    fullname:'',
    email:'',
    phone:'',
    message:''
  });

  const InputEvent = (event) => {
    const {name, value} = event.target;
    setData((preValue) => {
      return {
        ...preValue,
        [name]: value,
      }
      
    })
  }
  const formSubmit = (e) =>{
    e.preventDefault();
    alert(`My name is ${data.fullname} . My emailid is ${data.email}` );
  }
    return(
        <>
        <div className="my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>

          <div className="container conact-div">

            <div className="row">
              <div className="col-md-6 col-10 mx-auto">
                <form onSubmit={formSubmit}>
                  
              <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">Full Name</label>
                  <input type="text" class="form-control" id="exampleFormControlInput1" name ="fullname" value={data.fullname} onChange={InputEvent} placeholder="Enter Your Name" />
                </div>

                <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">Email address</label>
                  <input type="email" class="form-control" id="exampleFormControlInput1" name ="email" value={data.email} onChange={InputEvent} placeholder="Enter Your Email id" />
                </div>

                <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">Phone Number</label>
                  <input type="number" class="form-control" id="exampleFormControlInput1" name ="phone" value={data.phone} onChange={InputEvent} placeholder="Enter Your Phone Number" />
                </div>
                <div class="mb-3">
                  <label for="exampleFormControlTextarea1" class="form-label" name="message" value={data.message} onChange={InputEvent}>Example textarea</label>
                  <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                </div>

                <div class="col-12">
                  <button class="btn btn-outline-primary" type="submit">Submit form</button>
                </div>

                </form>
              </div>
            </div>

          </div>
        </>
    )
}

export default Contact;